import useVaultsStore from '@/store/vaults/useVaultsList'
import { QueryVaultListOptions, VaultApiInfo, VaultBaseInfo, VaultStatItem } from '@/types'
import { aprProcessing } from '@/utils/api-data-utils'
import { formatVaultApiInfo } from '@/utils/vaults'
import { useFetch } from '@cetus/hooks'
import { VAULT_FILTER } from '@cetus/types'
import { formatNumber, symbolDataDisplayProcessing } from '@cetus/utils'
import { d, fixCoinType } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { staticVaultsList } from '../../../src/constant/vaults'
import useFavoritePool from '../pool/useFavoritePool'

export default function useGetVaultList() {
  const { fetchByApi } = useFetch()
  const { getFavoritePoolList } = useFavoritePool()
  const { setVaultStatsObj, setVaultBaseList, vaultBaseList, vaultStatsObj, setVaultStatsLoading, setVaultBaseListLoading } = useVaultsStore()

  // 获取vaults基础信息列表
  const getVaultBaseList = async (showLoading: boolean = true, pools: string[], statsObj: any): Promise<VaultBaseInfo[]> => {
    try {
      if (showLoading) {
        setVaultBaseListLoading(true)
      }

      const res = await getFavoritePoolList({
        is_vaults: false,
        display_all_pools: false,
        has_mining: true,
        has_farming: true,
        no_incentives: true,
        order_by: '-vol',
        limit: 100,
        offset: 0,
        pools
      })

      const list: VaultBaseInfo[] = res?.list.map((item: any) => {
        const coinAB = [fixCoinType(item?.tokenA?.coin_type), fixCoinType(item?.tokenB?.coin_type)]
        const disPlayCoinAB = item.isReverse ? coinAB.reverse() : coinAB
        const farmingRewards = item?.miningRewardList?.map((fItem: any) => fixCoinType(fItem?.coinType))
        const miningRewards = item?.miningRewardList?.map((mItem: any) => fixCoinType(mItem?.coinType))
        const vaultsRewards = Array.from(new Set([...disPlayCoinAB, ...farmingRewards, ...miningRewards]))
        return {
          ...item,
          vaultsRewards, // vaults奖励，因为farming和mining奖励拿不到详细信息，为保持统一只有coinType
          category: statsObj[item.poolAddress]?.category // vault 来源 haedal cetus
        }
      })

      console.log('🚀 ~ file: useGetVaultList.ts:42 ~ getVaultBaseList ~ list:', list)

      if (list.length > 0) {
        setVaultBaseList(list)
      }

      return list
    } catch (error) {
      console.log('getVaultBaseList Error:', error)
    } finally {
      setVaultBaseListLoading(false)
    }
    return []
  }

  // 获取vaults统计信息(目前不支持单条查询, 详情可直接调用，然后在store的vaultsStatsObj中用key查询展示)
  const getVaultsStats = async () => {
    try {
      setVaultStatsLoading(true)
      const res = await fetchByApi('/v3/sui/vaults/pools', 'GET')
      const vaultsStatsObj: Record<string, VaultStatItem> = {}
      if (res?.list) {
        res?.list?.forEach((item: any) => {
          if (item.display || !VAULT_FILTER) {
            const clmmPoolAddress = item?.clmm_pool
            const vaultsId = item?.id
            // const vaultsTotalApy = d(item?.apy).div(100).toString()
            const vaultsTotalApy = item.category == 'cetus' ? d(item?.apy).add(d(item.lst_apy)).toString() : d(item.apr).add(d(item.apy)).toString()
            const vaultsTotalApyDisplay = aprProcessing(vaultsTotalApy, true, true)

            const vaultsApy = item?.apy
            const vaultsApyDisplay = aprProcessing(vaultsApy, true)

            const vaultsLstApy = item?.lst_apy
            const vaultsLstApyDisplay = aprProcessing(vaultsLstApy, true)

            const vaultsTvl = item?.tvl
            const vaultsTvlDisplay = symbolDataDisplayProcessing(vaultsTvl, '$')

            const vaultsApr = item?.apr
            const vaultsAprDisplay = aprProcessing(vaultsApr, true)

            const hardCapUSD = item?.hard_cap_usd
            // d(vaultsTvl).div(d(hardCapUSD)).mul(100).gt(100) ? '100' :
            const depositRatio = d(vaultsTvl).div(d(hardCapUSD)).mul(100)
            const depositRatioDisplay = formatNumber(depositRatio, 2)
            //  `${d(depositRatio).gt(100) ? '100' : formatNumber(depositRatio, 2)}%`

            vaultsStatsObj[clmmPoolAddress] = {
              vaultsApy,
              vaultsApyDisplay,
              vaultsTotalApy,
              vaultsTotalApyDisplay,
              vaultsLstApy,
              vaultsLstApyDisplay,
              vaultsApr,
              vaultsAprDisplay,
              clmmPoolAddress,
              vaultsId,
              vaultsTvl,
              vaultsTvlDisplay,
              amountPerLpA: item?.reward_per_lp?.amount_a_per_lp,
              amountPerLpB: item?.reward_per_lp?.amount_b_per_lp,
              category: item.category,
              hardCapUSD,
              depositRatio,
              depositRatioDisplay,
              lpTokentype: item?.lp_type
            }
          }
        })
        setVaultStatsObj(vaultsStatsObj)

        return {
          vaultsStatsObj,
          vaultsList: res?.list.filter((ele: any) => {
            return ele.display || !VAULT_FILTER
          })
        }
      }
    } catch (error) {
      console.log('🚀🚀🚀 ~ useGetVaultList.ts:129 ~ getVaultsStats ~ error:', error)
      return vaultStatsObj
    } finally {
      setVaultStatsLoading(false)
    }

    return {}
  }

  // 获取vaults列表
  const getVaultList = async (options: QueryVaultListOptions): Promise<VaultApiInfo[]> => {
    try {
      const { vaultsStatsObj: statsObj, vaultsList } = await getVaultsStats()
      const vaultsPoolList = vaultsList.map((vault: VaultStatItem) => vault.clmm_pool)
      // const baseList = vaultBaseList.length > 0 ? vaultBaseList : await getVaultBaseList(true, vaultsPoolList, statsObj)
      const baseList = await getVaultBaseList(true, vaultsPoolList, statsObj)

      return sortVaultApiInfoList(options, baseList, statsObj)
    } catch (error) {
      console.error('getVaultList Error:', error)
      return getVaultListFromCache(options)
    }
  }

  const getVaultListFromCache = (options: QueryVaultListOptions): VaultApiInfo[] => {
    try {
      const baseList = vaultBaseList
      const statsObj = vaultStatsObj
      if (baseList && baseList.length > 0) {
        return sortVaultApiInfoList(options, baseList, statsObj)
      } else {
        throw Error('Cache is null')
      }
    } catch (error) {
      console.error('getVaultListFromCache Error:', error)
      return staticVaultsList
    }
  }

  const sortVaultApiInfoList = (options: QueryVaultListOptions, baseList: any[], statsObj: any) => {
    const { sortRule, sortType } = options
    const list = baseList?.map((item: any) => {
      const statsItem = statsObj?.[item.poolAddress]
      return formatVaultApiInfo(item, statsItem)
    })

    const result = list?.sort((a: any, b: any) => {
      if (sortType === 'apr') {
        return sortRule === 'desc'
          ? b.vaultsTotalApy?.replace('%', '') - a.vaultsTotalApy?.replace('%', '')
          : a.vaultsTotalApy?.replace('%', '') - b.vaultsTotalApy?.replace('%', '')
      }

      return sortRule === 'desc' ? b.vaultsTvl - a.vaultsTvl : a.vaultsTvl - b.vaultsTvl
    })
    console.log('🚀🚀🚀 ~ useGetVaultList.ts:187 ~ sortVaultApiInfoList ~ result:', result)
    return result
  }

  return {
    getVaultBaseList,
    getVaultsStats,
    getVaultListFromCache,
    getVaultList
  }
}
