import { VaultBaseInfo, VaultStatItem } from '@/types'
import { Pool } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { Vault } from '@cetusprotocol/vaults-sdk'
import { StateCreator, create } from 'zustand'
import { persist } from 'zustand/middleware'

interface VaultsState {
  vaultStatsLoading: boolean
  setVaultStatsLoading: (isLoading: boolean) => void
  vaultStatsObj: Record<string, VaultStatItem>
  setVaultStatsObj: (data: Record<string, VaultStatItem>) => void

  vaultBaseListLoading: boolean
  setVaultBaseListLoading: (isLoading: boolean) => void
  vaultBaseList: VaultBaseInfo[]
  setVaultBaseList: (list: VaultBaseInfo[]) => void

  contractVaultObjLoading: boolean
  setContractVaultObjLoading: (isLoading: boolean) => void
  contractVaultObj: Record<string, Vault>
  setContractVaultObj: (vault: Vault) => void

  contractClmmPoolObjLoading: boolean
  setContractClmmPoolObjLoading: (isLoading: boolean) => void
  contractClmmPoolObj: Record<string, Pool>
  setContractClmmPoolObj: (pool: Pool) => void

  holdTotalValue: Record<string, string>
  setHoldTotalValue: (vaultId: string, value: string) => void

  detailRefreshCount: number
  setDetailRefreshCount: (count: number) => void

  lpTokenInfo: any
  setLpTokenInfo: (data: any) => void
}

const store: StateCreator<VaultsState> = (set, get) => ({
  detailRefreshCount: 0,
  contractClmmPoolObjLoading: false,
  contractVaultObjLoading: false,
  vaultBaseListLoading: false,
  vaultStatsLoading: false,
  holdVaultsObj: {},
  holdTotalValue: {},
  vaultStatsObj: {},
  contractClmmPoolObj: {},
  contractVaultObj: {},
  isYourHoldings: false,
  setVaultStatsObj: (data: Record<string, VaultStatItem>) => {
    set(() => ({
      vaultStatsObj: data
    }))
  },
  vaultBaseList: [],
  setVaultBaseList: (list: VaultBaseInfo[]) => {
    set(() => ({
      vaultBaseList: list
    }))
  },
  setVaultBaseListLoading: (isLoading: boolean) => {
    set(() => ({
      vaultBaseListLoading: isLoading
    }))
  },
  setVaultStatsLoading: (isLoading: boolean) => {
    set(() => ({
      vaultStatsLoading: isLoading
    }))
  },
  setContractVaultObj: (vault: Vault) => {
    const contractVaultObj = get().contractVaultObj
    contractVaultObj[vault.id] = vault
    set(() => ({
      contractVaultObj: { ...contractVaultObj }
    }))
  },
  setContractVaultObjLoading: (isLoading: boolean) => {
    set(() => ({
      contractVaultObjLoading: isLoading
    }))
  },
  setContractClmmPoolObjLoading: (isLoading: boolean) => {
    set(() => ({
      contractClmmPoolObjLoading: isLoading
    }))
  },
  setContractClmmPoolObj: (pool: Pool) => {
    const contractClmmPoolObj = get().contractClmmPoolObj
    contractClmmPoolObj[pool.poolAddress] = pool
    set(() => ({
      contractClmmPoolObj: { ...contractClmmPoolObj }
    }))
  },
  setHoldTotalValue: (vaultId: string, value: string) => {
    const holdTotalValue = get().holdTotalValue
    holdTotalValue[vaultId] = value
    set(() => ({
      holdTotalValue: { ...holdTotalValue }
    }))
  },
  setDetailRefreshCount: (count: number) => {
    set(() => ({
      detailRefreshCount: count
    }))
  },
  lpTokenInfo: {} as any,
  setLpTokenInfo: (data: any) => {
    set(() => ({
      lpTokenInfo: {
        ...get().lpTokenInfo,
        ...data
      }
    }))
  }
})

const useVaultsListStore = create(
  persist(store, {
    name: 'useVaultsStore2',
    partialize: state => {
      const { vaultBaseList, vaultStatsObj, contractVaultObj, contractClmmPoolObj, lpTokenInfo } = state
      return { vaultBaseList, vaultStatsObj, contractVaultObj, contractClmmPoolObj, lpTokenInfo }
    }
  })
)
export default useVaultsListStore
