import { CetusTooltip } from '@cetus/design'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { HTextLabelBox, SingleCoinImage } from '@cetus/ui-kit'
import { Center, HStack, Text, VStack } from '@chakra-ui/react'

interface VaultsAprBlockProps {
  vaultsLstApyDisplay: string
  vaultsApyDisplay: string
  vaultsTotalApyDisplay: string
  vaultsAprDisplay: string
  category?: string
}

function VaultsAprBlock({ vaultsLstApyDisplay, vaultsApyDisplay, vaultsTotalApyDisplay, category, vaultsAprDisplay }: VaultsAprBlockProps) {
  return (
    <CetusTooltip
      tooltip={
        <VaultsAprTooltipContent
          vaultsApyDisplay={vaultsApyDisplay}
          vaultsLstApyDisplay={vaultsLstApyDisplay}
          vaultsTotalApyDisplay={vaultsTotalApyDisplay}
          vaultsAprDisplay={vaultsAprDisplay}
          category={category}
        />
      }
    >
      <Text textColor="text_caption" textAlign="right" fontWeight="500" textDecoration="underline dotted" color="primary">
        {category == 'cetus' ? vaultsTotalApyDisplay : vaultsApyDisplay}
      </Text>
    </CetusTooltip>
  )
}

export const VaultsAprTooltipContent = ({
  vaultsLstApyDisplay,
  vaultsApyDisplay,
  vaultsTotalApyDisplay,
  category,
  vaultsAprDisplay
}: VaultsAprBlockProps) => {
  return (
    <VStack gap="12px" w="100%" pt="8px" pb="8px">
      {category == 'cetus' ? (
        <VStack>
          <HTextLabelBox
            label="Total APY"
            labelStyle={{
              fontSize: '14px'
            }}
            valueStyle={{
              fontSize: '14px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value={vaultsTotalApyDisplay}
          />
          <HStack h="1px" w="100%" backgroundColor="border" />
          <HTextLabelBox
            label="Fees and Rewards"
            labelStyle={{
              fontSize: '12px'
            }}
            valueStyle={{
              fontSize: '12px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value={vaultsApyDisplay}
          />
          <HTextLabelBox
            label="LST"
            labelStyle={{
              fontSize: '12px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value={vaultsLstApyDisplay}
            valueStyle={{
              fontSize: '12px'
            }}
          />
        </VStack>
      ) : (
        <VStack>
          <HTextLabelBox
            label="Cetus LP APR"
            labelStyle={{
              fontSize: '14px'
            }}
            valueStyle={{
              fontSize: '14px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value={vaultsAprDisplay}
          />
          <HStack h="1px" w="100%" backgroundColor="border" />
          <HTextLabelBox
            label="Vault APY"
            labelStyle={{
              fontSize: '14px'
            }}
            valueStyle={{
              fontSize: '14px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value={vaultsApyDisplay}
          />
          <HTextLabelBox
            label="(Compounding Cetus LP APR Multiple Times a Day)"
            labelStyle={{
              fontSize: '12px',
              lineHeight: '16px',
              maxW: '200px'
            }}
            wrapStyle={{
              gap: '16px'
            }}
            value=""
            valueStyle={{
              fontSize: '12px'
            }}
          />
        </VStack>
      )}
    </VStack>
  )
}

export function CoinImage({ coinType }: { coinType: string }) {
  const { tokenInfo } = useGetToken(coinType)
  return (
    <Center>
      <SingleCoinImage imageUrl={tokenInfo?.logo_url} w="20px" h="20px" />
    </Center>
  )
}

export default VaultsAprBlock
