import useTransaction from '@/hooks/common/useTransaction'
import useGlobalStore from '@/store/common/global'
import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { MsafeTransactionSubType } from '@/types'
import { AssetActionType, VaultCoinAmounts } from '@/types/vaults'
import { formatDescription } from '@/utils'
import { useDebounceFunction } from '@cetus/hooks'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { useAccountStore } from '@cetus/stores'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { BalanceChanges, CommonTypeInfo, ToastType, Token, TransactionStatusType } from '@cetus/types'
import { d, formatNumber, formatNumberWithDown, fromDecimalsAmountFix, getBalanceChanges } from '@cetus/utils'
import { toDecimalsAmount } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { CalculateRemoveAmountResult } from '@cetusprotocol/vaults-sdk'
import { WithdrawCalculationOptions, WithdrawOptions } from 'haedal-vault-sdk'
import { useEffect, useMemo, useRef, useState } from 'react'
import { v4 } from 'uuid'

export default function useVaultsRemove(
  assetAction: AssetActionType,
  autoRefresh: () => void,
  holdCoinAmounts: VaultCoinAmounts,
  holdLpAmount?: string,
  vaultsId?: string,
  tokenA?: Token,
  tokenB?: Token,
  isReverse?: boolean,
  isCheckedZAP?: boolean,
  contractClmmPoolInfo?: any
) {
  const { vaultsSlippage, mevProtect, maxCapForGas, transactionMode, customGasPrice } = useGlobalStore()
  const { currentAccount } = useAccountStore()
  const { setDetailRefreshCount } = useUnstableVaultsListStore()
  const { fetchTokenPrices, getTokenAmountValue } = useTokenPrice()
  const { volatileVaultsSdk } = usePeripherySDKStore()

  const [amountInputA, setAmountInputA] = useState<string>('')
  const [amountInputB, setAmountInputB] = useState<string>('')
  const [fixAmountA, setFixAmountA] = useState<boolean>(true)
  const [uuid, setUuid] = useState<string>('')
  const [isPercentInput, setIsPercentInput] = useState<boolean>(false)
  const [removePercent, setRemovePercent] = useState<number>(0)

  const uuidRef = useRef<string>(uuid)
  const fixAmountARef = useRef<boolean>(fixAmountA)
  const amountInputARef = useRef<string>(amountInputA)
  const amountInputBRef = useRef<string>(amountInputB)
  const isPercentInputRef = useRef<boolean>(isPercentInput)
  const removePercentRef = useRef<number>(removePercent)

  useEffect(() => {
    removePercentRef.current = removePercent
  }, [removePercent])

  useEffect(() => {
    isPercentInputRef.current = isPercentInput
  }, [isPercentInput])

  useEffect(() => {
    uuidRef.current = uuid
  }, [uuid])

  useEffect(() => {
    fixAmountARef.current = fixAmountA
  }, [fixAmountA])

  useEffect(() => {
    amountInputARef.current = isReverse ? amountInputA : amountInputA
  }, [amountInputA, isReverse, amountInputB])

  useEffect(() => {
    amountInputBRef.current = isReverse ? amountInputB : amountInputB
  }, [amountInputB, isReverse, amountInputA])

  //获取价格
  useEffect(() => {
    const coinTypeList: string[] = []
    if (tokenA && tokenB) {
      coinTypeList.push(tokenA.coin_type)
      coinTypeList.push(tokenB.coin_type)
    }
    if (coinTypeList.length > 0) {
      fetchTokenPrices(coinTypeList)
    }
  }, [tokenA?.coin_type, tokenB?.coin_type])

  // 可用数量
  const [availableAmountA, setAvailableAmountA] = useState<string>('0')
  const [availableAmountB, setAvailableAmountB] = useState<string>('0')
  const [calculateAvailableLoading, setCalculateAvailableLoading] = useState<boolean>(false)

  useEffect(() => {
    if (holdCoinAmounts && holdCoinAmounts?.coinAmountA && holdCoinAmounts?.coinAmountB) {
      calculateAvailableAmount()
    }
  }, [holdCoinAmounts?.coinAmountA, holdCoinAmounts?.coinAmountB])

  // 资产变更，计算 A B可用数量
  useEffect(() => {
    reCalculateResult()
  }, [contractClmmPoolInfo?.current_sqrt_price])

  // 计算可用资产
  const calculateAvailableAmount = async () => {
    console.log('🚀🚀🚀 ~ useVaultsRemove.ts:103 ~ calculateAvailableAmount ~ calculateAvailableAmount:')
    const { coinAmountA, coinAmountB } = holdCoinAmounts
    setCalculateAvailableLoading(false)
    if (assetAction === 'both') {
      setAvailableAmountA(isReverse ? coinAmountB : coinAmountA)
      setAvailableAmountB(isReverse ? coinAmountA : coinAmountB)
      return
    }

    // 利用findRouter 转化为 单一资产
    const isOnlyReceiveCoinA = assetAction === tokenA?.coin_type
    const fromAmount = isOnlyReceiveCoinA ? coinAmountB : coinAmountA
    const targetAmount = isOnlyReceiveCoinA ? coinAmountA : coinAmountB
    if (d(fromAmount).gt(0)) {
      setCalculateAvailableLoading(true)
      const uuid = v4()
      setUuid(uuid)
      // mode为合约方向
      const mode = assetAction == tokenA?.coin_type ? 'OnlyCoinA' : 'OnlyCoinB'
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:127 ~ calculateAvailableAmount ~ tokenA?.coin_type :', tokenA?.coin_type)
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:127 ~ calculateAvailableAmount ~ assetAction:', assetAction)
      const params: any = {
        pool_id: vaultsId,
        mode,
        burn_lp_amount: holdLpAmount,
        available_lp_amount: holdLpAmount
      }
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:131 ~ calculateAvailableAmount ~ params:', params)
      try {
        const res = await volatileVaultsSdk.Vaults.preCalculateWithdrawAmount(params)
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:138 ~ calculateAvailableAmount ~ res:', res)
        // mode 为合约方向
        if (mode == 'OnlyCoinA') {
          if (tokenA?.decimals === undefined) throw new Error('tokenA.decimals is undefined')
          const amount = fromDecimalsAmountFix(res.receive_amount_a, tokenA?.decimals)
          if (isReverse) {
            setAvailableAmountB(amount)
          } else {
            setAvailableAmountA(amount)
          }
        } else {
          if (tokenB?.decimals === undefined) throw new Error('tokenB.decimals is undefined')
          const amount = fromDecimalsAmountFix(res.receive_amount_b, tokenB?.decimals)
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:148 ~ calculateAvailableAmount ~ amount:', amount)
          if (isReverse) {
            setAvailableAmountA(amount)
          } else {
            setAvailableAmountB(amount)
          }
        }
        setCalculateAvailableLoading(false)
      } catch (error) {
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:145 ~ calculateAvailableAmount ~ error:', error)
      }
    } else {
      if (isOnlyReceiveCoinA) {
        setAvailableAmountA(targetAmount)
      } else {
        setAvailableAmountB(targetAmount)
      }
    }
  }

  // 价值
  const amountValueA = getTokenAmountValue(isReverse ? tokenB?.coin_type : tokenA?.coin_type, amountInputA)
  const amountValueB = getTokenAmountValue(isReverse ? tokenA?.coin_type : tokenB?.coin_type, amountInputB)
  // 总价值ß
  const inputTotalValue = useMemo(() => {
    return d(amountValueA || '0')
      .add(amountValueB || '0')
      .toString()
  }, [amountValueA, amountValueB])
  console.log('🚀🚀🚀 ~ useVaultsRemove.ts:170 ~ inputTotalValue ~ amountValueB:', amountValueB)
  console.log('🚀🚀🚀 ~ useVaultsRemove.ts:170 ~ inputTotalValue ~ amountValueA:', amountValueA)

  // 预计算结果
  const [preCalculateLoading, setPreCalculateLoading] = useState<boolean>(false)
  const [calculateResult, setCalculateResult] = useState<CalculateRemoveAmountResult | undefined>(undefined)

  /**
   * 预计算
   * @param amount
   * @param isPercentInput
   * @param fix_amount_a
   * @param uuid
   */
  const preCalculate = async (
    amount: string,
    is_ft_input: boolean,
    fix_amount_a: boolean,
    uuid: string,
    removePercent?: number,
    isSlider?: boolean
  ) => {
    console.log('🚀🚀🚀 ~ useVaultsRemove.ts:210 ~ removePercent:', removePercent)
    // fix_amount_a为合约方向
    if (d(removePercent).gt(100)) {
      setUuid('')
      setCalculateResult(undefined)
      setPreCalculateLoading(false)
      return
    }
    if (tokenA && tokenB && vaultsId && holdLpAmount) {
      // 非百分比输入，要判断余额是否足够
      if (!removePercent) {
        if (fix_amount_a) {
          if (d(amount).gt(isReverse ? availableAmountB : availableAmountA)) {
            setUuid('')
            setCalculateResult(undefined)
            setPreCalculateLoading(false)
            return
          }
        } else {
          if (d(amount).gt(isReverse ? availableAmountA : availableAmountB)) {
            setUuid('')
            setCalculateResult(undefined)
            setPreCalculateLoading(false)
            return
          }
        }
      }

      setDetailRefreshCount(0)
      if (+amountInputARef.current || +amountInputBRef.current || removePercent) {
        setPreCalculateLoading(true)
      }
      const fixToken = fix_amount_a ? tokenA : tokenB
      const inputAmount = is_ft_input ? formatNumberWithDown(amount, 0, true) : toDecimalsAmount(amount, fixToken.decimals).toFixed(0)

      let params: WithdrawCalculationOptions

      if (assetAction == 'both') {
        params = {
          pool_id: vaultsId,
          mode: 'FixedOneSide',
          burn_lp_amount: String(inputAmount)
        }
      } else {
        const mode = assetAction == tokenA?.coin_type ? 'OnlyCoinA' : 'OnlyCoinB'
        params = {
          pool_id: vaultsId,
          mode,
          burn_lp_amount: String(inputAmount)
        }
      }

      console.log('🚀 ~ preCalculate ~ params:', params, uuid, removePercent)
      try {
        const res = await volatileVaultsSdk.Vaults.preCalculateWithdrawAmount(params)
        console.log('🚀 ~ preCalculate ~ res:', {
          ...res,
          uuid,
          assetAction
        })

        const wrapRes: any = {
          amount_a: isReverse ? res.receive_amount_b : res.receive_amount_a,
          amount_b: isReverse ? res.receive_amount_a : res.receive_amount_b,
          burn_ft_amount: res.burn_lp_amount,
          swap: res.swap
        }

        if (wrapRes.burn_ft_amount == '0') {
          setPreCalculateLoading(false)
          setCalculateResult(undefined)
          return
        }

        // if (uuidRef.current === uuid) {
        const amountA = fromDecimalsAmountFix(wrapRes.amount_a.toString(), isReverse ? tokenB.decimals : tokenA.decimals).toString()
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:271 ~ preCalculate ~ tokenA:', tokenA)
        const amountB = fromDecimalsAmountFix(wrapRes.amount_b.toString(), isReverse ? tokenA.decimals : tokenB.decimals).toString()
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:256 ~ preCalculate ~ amountA:', amountA, tokenA.decimals, amountB, tokenB?.decimals)
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:258 ~ preCalculate ~ amountB:', amountB)
        // 双边
        if (assetAction === 'both') {
          if (is_ft_input && !isSlider) {
            console.log('🚀🚀🚀 ~ useVaultsRemove.ts:284 ~ preCalculate ~ is_ft_input:', is_ft_input)
            const fix_amount_a_page = isReverse ? !fix_amount_a : fix_amount_a
            if (fix_amount_a_page) {
              console.log('🚀🚀🚀 ~ useVaultsRemove.ts:289 ~ preCalculate ~ +amountInputARef.current:', +amountInputARef.current)
              if (+amountInputARef.current) {
                console.log('🚀🚀🚀 ~ useVaultsRemove.ts:293 ~ preCalculate ~ setAmountInputB:')
                setAmountInputB(fromDecimalsAmountFix(wrapRes.amount_b.toString(), isReverse ? tokenA?.decimals : tokenB?.decimals).toString())
                setCalculateResult(wrapRes)
              } else {
                setAmountInputB('')
              }
            } else {
              if (+amountInputBRef.current) {
                setAmountInputA(fromDecimalsAmountFix(wrapRes.amount_a.toString(), isReverse ? tokenB?.decimals : tokenA?.decimals).toString())
                setCalculateResult(wrapRes)
              } else {
                setAmountInputA('')
              }
            }
            if (removePercent === 100) {
              // 去掉了这个功能 修改最大可用之后会触发重新计算 可能会死循环
              // 由于价差原因，当移除100% 时， 需要修改最大可用
              // setAvailableAmountA(amountA)
              // setAvailableAmountB(amountB)
            }
          } else {
            setCalculateResult(wrapRes)
          }
          // }
        }
        // 单边
        if (assetAction !== 'both') {
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:336 ~ preCalculate ~ removePercent:', removePercent)
          if (removePercent) {
            const amountA = fromDecimalsAmountFix(
              d(wrapRes.amount_a)
                .add(wrapRes.amount_b || '0')
                .toString(),
              isReverse ? tokenB.decimals : tokenA.decimals
            ).toString()
            console.log('🚀🚀🚀 ~ useVaultsRemove.ts:344 ~ preCalculate ~ amountA:', amountA)
            if (isReverse ? !fix_amount_a : fix_amount_a) {
              if (removePercent === 100) {
                wrapRes.burn_ft_amount = holdLpAmount
              }
            } else {
              const amountB = fromDecimalsAmountFix(
                d(wrapRes.amount_a)
                  .add(wrapRes.amount_b || '0')
                  .toString(),
                isReverse ? tokenA.decimals : tokenB.decimals
              ).toString()
              if (removePercent === 100) {
                wrapRes.burn_ft_amount = holdLpAmount
              }
            }
          }
          setCalculateResult(wrapRes)
        }
      } catch (error) {
        console.log('🚀 ~ preCalculate ~ error:', error)
        setCalculateResult(undefined)
      } finally {
        setPreCalculateLoading(false)
      }
    }
  }
  const debouncedPreCalculate = useDebounceFunction(preCalculate, 500)

  // 输入数量监听
  const handleAmountInputChange = (
    amount: string,
    fix_amount_a: boolean,
    removeMax: boolean = false,
    isHalfClickMax: boolean = false,
    resetTargetInput: boolean = true
  ) => {
    // 移除全部，走LP 移除
    if (removeMax) {
      handlePercentInputChange(100)
      return
    }

    if (isHalfClickMax) {
      handlePercentInputChange(50)
      return
    }

    setFixAmountA(fix_amount_a)
    setIsPercentInput(false)

    if (isReverse ? !fix_amount_a : fix_amount_a) {
      setAmountInputA(amount)
      if (resetTargetInput) {
        setAmountInputB('')
      } else {
        reCalculateResult()
      }
    } else {
      setAmountInputB(amount)
      if (resetTargetInput) {
        setAmountInputA('')
      } else {
        reCalculateResult()
      }
    }
    let removeAmount, removeRatio
    if (isReverse ? !fix_amount_a : fix_amount_a) {
      removeRatio = d(amount).div(availableAmountA)
      removeAmount = formatNumberWithDown(d(holdLpAmount).mul(removeRatio).toString(), 0, true)
    } else {
      removeRatio = d(amount).div(availableAmountB)
      removeAmount = formatNumberWithDown(d(holdLpAmount).mul(removeRatio).toString(), 0, true)
    }

    if (+removeAmount) {
      const uuid = v4()
      setUuid(uuid)
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:466 ~ removeLpAmount:', removeAmount)
      debouncedPreCalculate(String(removeAmount), true, fix_amount_a, uuid, removeRatio.mul(100).toNumber(), removeRatio.gte(100))
    } else {
      resetInputAmount()
    }
  }

  // 百分比输入监听
  const handlePercentInputChange = (percent: number) => {
    setIsPercentInput(true)
    console.log('🚀🚀🚀 ~ useVaultsRemove.ts:474 ~ handlePercentInputChange ~ percent:', percent)
    setRemovePercent(percent)

    if (holdLpAmount && +holdLpAmount) {
      if (percent === 0) {
        resetInputAmount()
      } else {
        // fix_amount_a为合约方向
        const fix_amount_a = assetAction === tokenA?.coin_type
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:415 ~ handlePercentInputChange ~ fix_amount_a:', fix_amount_a)
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:515 ~ handlePercentInputChange ~ availableAmountB:', availableAmountB)
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:515 ~ handlePercentInputChange ~ availableAmountA:', availableAmountA)
        setFixAmountA(fix_amount_a)
        if (assetAction == 'both') {
          setAmountInputA(d(availableAmountA).mul(d(percent).div(100)).toString())
          setAmountInputB(d(availableAmountB).mul(d(percent).div(100)).toString())
        } else if (assetAction === tokenA?.coin_type) {
          isReverse
            ? setAmountInputB(d(availableAmountB).mul(d(percent).div(100)).toString())
            : setAmountInputA(d(availableAmountA).mul(d(percent).div(100)).toString())
        } else {
          isReverse
            ? setAmountInputA(d(availableAmountA).mul(d(percent).div(100)).toString())
            : setAmountInputB(d(availableAmountB).mul(d(percent).div(100)).toString())
        }

        // 双边和100% 按照LP 移除
        if (percent === 100 || assetAction === 'both') {
          const uuid = v4()
          setUuid(uuid)
          const removeLpAmount = percent === 100 ? holdLpAmount : d(holdLpAmount).mul(percent).div(100).toFixed(0)
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:521 ~ handlePercentInputChange ~ removeLpAmount:', removeLpAmount)
          debouncedPreCalculate(removeLpAmount, true, fix_amount_a, uuid, percent, true)
        } else {
          setFixAmountA(fix_amount_a)
          // 单边  按照最大金额的百分比移除

          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:466 ~ handlePercentInputChange ~ assetAction:', assetAction)
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:467 ~ handlePercentInputChange ~ tokenA?.coin_type:', tokenA?.coin_type)
          const getAmount = d(holdLpAmount).mul(percent).div(100).toFixed()
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:455 ~ handlePercentInputChange ~ getAmount:', getAmount)
          debouncedPreCalculate(getAmount, true, fix_amount_a, uuid, percent, true)
        }
      }
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:520 ~ handlePercentInputChange ~ availableAmountA:', availableAmountA)
    }
  }

  // 重新计算
  const reCalculateResult = () => {
    console.log('🚀 ~ reCalculateResult ~ isPercentInput:', { isPercentInput, removePercent })
    if (isPercentInput) {
      handlePercentInputChange(removePercent)
    } else {
      const amount = fixAmountARef.current
        ? isReverse
          ? amountInputBRef.current
          : amountInputARef.current
        : isReverse
          ? amountInputARef.current
          : amountInputBRef.current
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:510 ~ reCalculateResult ~ amount:', amount)
      handleAmountInputChange(amount, fixAmountARef.current)
    }
  }

  // 不是百分比输入，则需要计算百分比进度
  useEffect(() => {
    if (!isPercentInput) {
      if (calculateResult && holdLpAmount && +holdLpAmount) {
        let percent = Number(d(calculateResult.burn_ft_amount).div(holdLpAmount).mul(100))
        if (percent > 100) {
          percent = 100
        }
        console.log('🚀🚀🚀 ~ useVaultsRemove.ts:502 ~ useEffect ~ percent:', percent)
        setRemovePercent(percent)
      } else {
        setRemovePercent(0)
      }
    }
  }, [calculateResult?.burn_ft_amount, isPercentInput])

  const [submitLoading, setSubmitLoading] = useState<boolean>(false)
  const { signAndExecuteTransaction, transactionConfirmation, transactionRejected } = useTransaction()
  // 提交交易
  const doRemoveAction = async () => {
    if (vaultsId && calculateResult && holdLpAmount) {
      setSubmitLoading(true)

      let param: WithdrawOptions
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:567 ~ doRemoveAction ~ calculateResult:', calculateResult)
      if (assetAction === 'both') {
        param = {
          pool_id: vaultsId,
          burn_lp_amount: calculateResult.burn_ft_amount,
          mode: 'FixedOneSide',
          slippage: Number(vaultsSlippage)
        }
      } else {
        const fix_amount_a = assetAction === tokenA?.coin_type
        if (removePercent === 100) {
          param = {
            pool_id: vaultsId,
            burn_lp_amount: holdLpAmount,
            mode: assetAction == tokenA?.coin_type ? 'OnlyCoinA' : 'OnlyCoinB',
            slippage: Number(vaultsSlippage)
          }
        } else {
          param = {
            pool_id: vaultsId,
            burn_lp_amount: calculateResult.burn_ft_amount,
            mode: assetAction == tokenA?.coin_type ? 'OnlyCoinA' : 'OnlyCoinB',
            slippage: Number(vaultsSlippage)
          }
        }
      }

      console.log('🚀 ~ doRemoveAction ~ params:', JSON.stringify(param))

      // 构建 交易提示
      const toastInfo: ToastType = {
        getShowInfo: (status: TransactionStatusType, balanceChanges?: Record<string, BalanceChanges>) => {
          const description =
            'Withdraw ' +
            [
              formatDescription(isReverse ? amountInputA : amountInputA, isReverse ? tokenB?.symbol : tokenA?.symbol),
              formatDescription(isReverse ? amountInputB : amountInputB, isReverse ? tokenA?.symbol : tokenB?.symbol)
            ]
              .filter(Boolean)
              .join(' and ')

          const info: CommonTypeInfo = {
            modalDescriptionText: description,
            toastDescriptionContent: description
          }

          if (status === 'success') {
            let amountA = amountInputA
            let amountB = amountInputB

            // if (res) {
            //   res.events.forEach(event => {
            //     console.log('🚀🚀🚀 ~ useVaultsAdd.ts:367 ~ doAddAction ~ event:', event)
            //     if (event.type.indexOf('::pool::WithdrawEvent') > -1 && tokenA && tokenB) {
            //       amountA = String(fromDecimalsAmount(event.parsedJson.amount_a, tokenA.decimals))
            //       console.log('🚀🚀🚀 ~ useVaultsAdd.ts:361 ~ doAddAction ~ amountA:', amountA)
            //       amountB = String(fromDecimalsAmount(event.parsedJson.amount_b, tokenB.decimals))
            //       console.log('🚀🚀🚀 ~ useVaultsAdd.ts:363 ~ doAddAction ~ amountB:', amountB)
            //     }
            //   })
            // }

            console.log('🚀🚀🚀 ~ useVaultsRemove.ts:582 ~ doRemoveAction ~ balanceChanges:', balanceChanges)
            if (balanceChanges) {
              amountA = d(amountInputA).gt(0) ? getBalanceChanges(balanceChanges, isReverse ? tokenB : tokenA) || amountInputA : ''
              amountB = d(amountInputB).gt(0) ? getBalanceChanges(balanceChanges, isReverse ? tokenA : tokenB) || amountInputB : ''
            }
            console.log('🚀🚀🚀 ~ useVaultsRemove.ts:584 ~ doRemoveAction ~ amount:', amountA, amountB)

            const descriptionList = [
              formatDescription(isReverse ? amountB : amountA, isReverse ? tokenB?.symbol : tokenA?.symbol),
              formatDescription(isReverse ? amountA : amountB, isReverse ? tokenA?.symbol : tokenB?.symbol)
            ].filter(Boolean)

            const description =
              (descriptionList && descriptionList.length > 1 ? 'Removed ' : 'Withdraw ') +
              [
                formatDescription(isReverse ? amountA : amountA, isReverse ? tokenB?.symbol : tokenA?.symbol),
                formatDescription(isReverse ? amountB : amountB, isReverse ? tokenA?.symbol : tokenB?.symbol)
              ]
                .filter(Boolean)
                .join(' and ')

            info.toastDescriptionContent = description
            info.modalDescriptionText = description
          }

          return info
        }
      }
      transactionConfirmation(toastInfo)

      try {
        const txb = await volatileVaultsSdk.Vaults.buildWithdrawPayload(param)
        const res = await signAndExecuteTransaction(txb, toastInfo, {
          useMev: mevProtect,
          txAction: 'signTransactionBlock',
          useFastMode: transactionMode === 'Fast Mode',
          maxCapForGas,
          customGasPrice,
          msafeParams: {
            action: MsafeTransactionSubType.AddVaultsPosition,
            txbParams: param
          }
        })

        if (res) {
          resetInputAmount()
          autoRefresh()
        } else {
          reCalculateResult()
        }
        setSubmitLoading(false)
      } catch (error) {
        console.log('🚀 ~ doAddAction ~ error:', error)
        setSubmitLoading(false)
        transactionRejected(toastInfo)
      }
    }
  }

  // 清空输入框
  const resetInputAmount = () => {
    setAmountInputA('')
    setAmountInputB('')
    amountInputARef.current = ''
    amountInputBRef.current = ''
    setRemovePercent(0)
    setPreCalculateLoading(false)
    setCalculateResult(undefined)
    setUuid('')
    console.log('🚀🚀🚀 ~ useVaultsRemove.ts:697 ~ resetInputAmount ~ resetInputAmount:')
  }

  // 单边提示文案
  const showOnlySideTips = useMemo(() => {
    console.log('🚀🚀🚀 ~ useVaultsRemove.ts:622 ~ showOnlySideTips ~ calculateResult:', calculateResult)
    if (calculateResult && calculateResult?.swap && assetAction !== 'both' && tokenA && tokenB) {
      const amount = fixAmountA ? (!isReverse ? amountInputA : amountInputB) : !isReverse ? amountInputB : amountInputA
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:653 ~ showOnlySideTips ~ amount:', amount)

      const fixToken = fixAmountARef.current ? tokenA : tokenB
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:651 ~ showOnlySideTips ~ fixToken:', fixToken)
      const targetToken = fixAmountARef.current ? tokenB : tokenA
      console.log('🚀🚀🚀 ~ useVaultsRemove.ts:652 ~ showOnlySideTips ~ targetToken:', targetToken)

      const swapInAmount = fromDecimalsAmountFix(calculateResult?.swap?.swap_amount_in, targetToken.decimals)

      const swapOutAmount = formatNumber(
        fromDecimalsAmountFix(
          d(calculateResult.swap.swap_amount_out)
            .mul(1 - Number(vaultsSlippage))
            .toString(),
          fixToken.decimals
        ),
        fixToken.decimals
      )

      console.log('vaultsSlippage: ', {
        vaultsSlippage,
        calculateResult,
        swapOutAmount
      })

      console.log(
        '🚀🚀🚀 ~ useVaultsRemove.ts:644 ~ showOnlySideTips ~ ',
        `To withdraw ${amount} ${fixToken?.symbol}, ${swapInAmount} ${targetToken?.symbol} in the pool will be traded for ${swapOutAmount} ${fixToken?.symbol}`
      )
      return `To withdraw ${formatNumber(amount)} ${fixToken?.symbol}, ${formatNumber(swapInAmount)} ${targetToken?.symbol} in the pool will be traded for ${swapOutAmount} ${fixToken?.symbol}`
    }
    return undefined
  }, [assetAction, calculateResult, amountInputA, amountInputB])

  useEffect(() => {
    reCalculateResult()
  }, [availableAmountA, availableAmountB])

  useEffect(() => {
    resetInputAmount()
    setTimeout(() => {
      if (holdCoinAmounts?.coinAmountA && holdCoinAmounts?.coinAmountB) {
        calculateAvailableAmount()
      }
    }, 200)
  }, [isCheckedZAP, assetAction, currentAccount?.address])

  return {
    handleAmountInputChange,
    fixAmountA,
    amountInputA,
    amountInputB,
    availableAmountA,
    availableAmountB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    inputTotalValue,
    calculateResult,
    showOnlySideTips,
    doRemoveAction,
    handlePercentInputChange,
    isPercentInput,
    removePercent,
    reCalculateResult,
    calculateAvailableLoading,
    submitLoading
  }
}
