import { AssetActionType } from '@/types/vaults'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { d } from '@cetus/utils'
import { useMemo } from 'react'

export default function useUnstableAddButtonStatus(
  isCheckedZAP: boolean,
  assetAction: AssetActionType,
  isAdd: boolean,
  amountInputA: string,
  amountInputB: string,
  fixAmountA: boolean,
  balanceA?: string,
  balanceB?: string,
  tokenA?: Token,
  tokenB?: Token,
  zapNumError?: string,
  calculateResult?: any,
  overHardtop?: boolean,
  inputTotalValueError?: boolean
) {
  const { currentAccount } = useAccountStore()

  // 按钮状态
  const submitBtnInfo = useMemo(() => {
    const btnInfo: {
      text?: string
      disabled: boolean
    } = {
      text: undefined,
      disabled: false
    }

    // 判断钱包
    if (!currentAccount?.address) {
      btnInfo.text = 'Connect Wallet'
      btnInfo.disabled = false
      return btnInfo
    }

    if (!tokenA || !tokenB) {
      btnInfo.disabled = true
      return btnInfo
    }

    // 判断输入
    // 双边
    if (assetAction === 'both') {
      if (!+amountInputA && !+amountInputB) {
        btnInfo.text = 'Enter an amount'
        btnInfo.disabled = true
        return btnInfo
      }
      if (isCheckedZAP && (!+amountInputA || !+amountInputB)) {
        btnInfo.text = 'Enter an amount'
        btnInfo.disabled = true
        return btnInfo
      }
    }
    // 单边输入
    if (assetAction !== 'both') {
      if (!(+amountInputA || +amountInputB)) {
        btnInfo.text = 'Enter an amount'
        btnInfo.disabled = true
        return btnInfo
      }
    }

    // 判断余额
    // 双边
    if (assetAction === 'both') {
      if (d(amountInputA).gt(balanceA || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenA?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
      if (d(amountInputB).gt(balanceB || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenB?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
    }
    // 单边
    if (assetAction !== 'both') {
      if (d(amountInputA).gt(balanceA || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenA?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
      if (d(amountInputB).gt(balanceB || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenB?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
    }

    //ZAP模式添加 超过$10000
    if (isAdd && zapNumError && !overHardtop) {
      btnInfo.text = 'Enter an amount'
      btnInfo.disabled = true
      return btnInfo
    }

    //ZAP模式添加 单token质押是否小于0.0001
    if (isAdd && isCheckedZAP && inputTotalValueError) {
      btnInfo.text = 'Enter an amount'
      btnInfo.disabled = true
      return btnInfo
    }

    //ZAP模式添加 预计算结果为0
    if ((calculateResult && calculateResult.ft_amount == '0') || (calculateResult && calculateResult.burn_ft_amount == '0') || !calculateResult) {
      btnInfo.disabled = true
      btnInfo.text = 'Enter an amount'
      return btnInfo
    }

    return btnInfo
  }, [currentAccount?.address, assetAction, amountInputA, amountInputB, balanceA, balanceB, calculateResult, isAdd, zapNumError])

  return {
    btnText: submitBtnInfo.text,
    btnDisabled: submitBtnInfo.disabled
  }
}
