import useVaultsListStore from '@/store/vaults/useVaultsList'
import { ContractVaultInfo } from '@/types/vaults'
import { calcCoinProportion } from '@/utils/pool'
import { useGetToken } from '@cetus/hooks/src/useToken'
import { useGetTokenBalance } from '@cetus/hooks/src/useTokenBalance'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { Token } from '@cetus/types'
import { fromDecimalsAmountFix, symbolDataDisplayProcessing } from '@cetus/utils'
import { ClmmPoolUtil, TickMath, d } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { VaultsUtils } from '@cetusprotocol/vaults-sdk'
import BN from 'bn.js'
import { useEffect, useMemo } from 'react'
import { useContractClmmInfo, useContractVault } from './useVaultsHelper'

/**
 * 通过合约数据，计算vaults相关属性
 * @param vaultsId
 * @param poolAddress
 * @param tokenA
 * @param tokenB
 * @param isReverse
 * @returns
 */
export default function useGetContractStableVaultInfo(
  vaultsId?: string,
  poolAddress?: string,
  tokenA?: Token,
  tokenB?: Token,
  isReverse: boolean = false
) {
  const { vaultsSdk } = usePeripherySDKStore()
  const { getTokenAmountValue } = useTokenPrice()

  const {
    setContractVaultObjLoading,
    setContractVaultObj,
    setContractClmmPoolObj,
    setContractClmmPoolObjLoading,
    setHoldTotalValue,
    holdTotalValue,
    setLpTokenInfo
  } = useVaultsListStore()

  // 从合约获取Vault 信息
  const fetchVaultInfoFromContract = async (vaults_id: string, forceRefresh = false) => {
    setContractVaultObjLoading(true)
    const info = await vaultsSdk.Vaults.getVault(vaults_id, forceRefresh)

    console.log('🚀 ~ file: useGetVaultInfo.ts:13 ~ fetchVaultInfoFromContract ~ info:', info)

    if (info) {
      setContractVaultObj(info)
    }
    setContractVaultObjLoading(false)
  }

  // 从合约获取clmm 池子信息
  const fetchClmmPoolFromContract = async (clmm_pool_id: string, forceRefresh = false) => {
    setContractClmmPoolObjLoading(true)
    const info = await vaultsSdk.Pool.getPool(clmm_pool_id, forceRefresh)

    console.log('🚀 ~ file: useGetVaultInfo.ts:33 ~ fetchClmmPoolFromContract ~ info:', info)

    if (info) {
      setContractClmmPoolObj(info)
    }
    setContractClmmPoolObjLoading(false)
  }

  // 合约vaults 信息
  const { contractVault } = useContractVault(vaultsId)
  console.log('🚀🚀🚀 ~ useGetContractVaultInfo.ts:71 ~ contractVault:', contractVault)

  // lp token信息
  const { tokenInfo: lpTokenInfo } = useGetToken(contractVault?.lp_token_type)

  // lp token 余额
  const { balanceInfo } = useGetTokenBalance(lpTokenInfo)

  //持仓百分比
  const sharePoolRate = useMemo(() => {
    if (contractVault && balanceInfo) {
      return d(balanceInfo.balance).div(contractVault.total_supply).mul(100).toString()
    }

    return '0'
  }, [contractVault, balanceInfo])

  // ClmmPool 信息
  const { contractClmmInfo } = useContractClmmInfo(poolAddress)

  // 根据流动性计算Token A B 数量
  const calculateCoinAmounts = (liquidity: string) => {
    if (contractVault && contractClmmInfo && tokenA && tokenB && isReverse !== undefined) {
      const curSqrtPrice = new BN(contractClmmInfo.current_sqrt_price)
      const lowerSqrtPrice = TickMath.tickIndexToSqrtPriceX64(contractVault.position.tick_lower_index)
      const upperSqrtPrice = TickMath.tickIndexToSqrtPriceX64(contractVault.position.tick_upper_index)
      const coinAmounts = ClmmPoolUtil.getCoinAmountFromLiquidity(new BN(liquidity), curSqrtPrice, lowerSqrtPrice, upperSqrtPrice, false)

      const coinAmountA = fromDecimalsAmountFix(coinAmounts.coinA.toString(), tokenA.decimals).toString()
      const coinAmountB = fromDecimalsAmountFix(coinAmounts.coinB.toString(), tokenB.decimals).toString()

      const displayCoinA = isReverse ? coinAmountB : coinAmountA
      const displayCoinB = isReverse ? coinAmountA : coinAmountB

      return {
        coinAmountA,
        coinAmountB,
        displayCoinA,
        displayCoinB
      }
    }
    return undefined
  }

  // 用户持有的coin A B 数量
  const holdCoinAmounts = useMemo(() => {
    if (contractVault && balanceInfo && contractClmmInfo) {
      const liquidity = VaultsUtils.get_share_liquidity_by_amount(contractVault, balanceInfo.balance)
      const info = calculateCoinAmounts(liquidity)
      if (info) {
        return info
      }
    }
    return {
      coinAmountA: '0',
      coinAmountB: '0',
      displayCoinA: '0',
      displayCoinB: '0'
    }
  }, [balanceInfo?.balance, contractClmmInfo?.current_sqrt_price])

  // 池子持有的coin A B 数量
  const vaultCoinAmounts = useMemo(() => {
    if (contractVault && contractClmmInfo) {
      const info = calculateCoinAmounts(contractVault.liquidity)
      if (info) {
        return info
      }
    }
    return undefined
  }, [contractVault?.liquidity, contractClmmInfo?.current_sqrt_price])

  // 当前价格
  const currPrice = useMemo(() => {
    if (contractClmmInfo && tokenA && tokenB) {
      return TickMath.sqrtPriceX64ToPrice(new BN(contractClmmInfo.current_sqrt_price), tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractClmmInfo?.current_sqrt_price, tokenA?.decimals])

  //最小价格
  const minPrice = useMemo(() => {
    if (contractVault && tokenA && tokenB) {
      console.log(
        '🚀🚀🚀 ~ useGetContractVaultInfo.ts:156 ~ minPrice ~ TickMath.tickIndexToPrice(contractVault.position.tick_lower_index, tokenA.decimals, tokenB.decimals).toString():',
        TickMath.tickIndexToPrice(contractVault.position.tick_lower_index, tokenA.decimals, tokenB.decimals).toString()
      )
      return TickMath.tickIndexToPrice(contractVault.position.tick_lower_index, tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractVault?.position.tick_lower_index, tokenA?.decimals])

  //最大价格
  const maxPrice = useMemo(() => {
    if (contractVault && tokenA && tokenB) {
      return TickMath.tickIndexToPrice(contractVault.position.tick_upper_index, tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractVault?.position.tick_upper_index, tokenA?.decimals])

  // 用户持有的coin A B 价值
  const holdCoinAValue = getTokenAmountValue(tokenA?.coin_type, holdCoinAmounts.coinAmountA)
  const holdCoinBValue = getTokenAmountValue(tokenB?.coin_type, holdCoinAmounts.coinAmountB)

  // 池子持有的coin A B 价值
  const vaultsCoinAValue = getTokenAmountValue(tokenA?.coin_type, vaultCoinAmounts?.coinAmountA)
  const vaultsCoinBValue = getTokenAmountValue(tokenB?.coin_type, vaultCoinAmounts?.coinAmountB)

  // 池子Token占比
  const vaultsCoinPercent = useMemo(() => {
    if (currPrice && vaultCoinAmounts) {
      return calcCoinProportion(vaultCoinAmounts.coinAmountA, vaultCoinAmounts.coinAmountB, currPrice, false)
    }

    return undefined
  }, [currPrice, vaultCoinAmounts])

  // 池子tvl
  const vaultTvl = useMemo(() => {
    return symbolDataDisplayProcessing(d(vaultsCoinAValue).add(vaultsCoinBValue).toString(), '$')
  }, [vaultsCoinAValue, vaultsCoinBValue])
  console.log('🚀🚀🚀 ~ useGetContractVaultInfo.ts:263 ~ vaultTvl ~ vaultTvl:', vaultTvl)

  useEffect(() => {
    if (vaultsId) {
      setHoldTotalValue(vaultsId, d(holdCoinAValue).add(holdCoinBValue).toString())
    }
  }, [holdCoinAValue, holdCoinBValue, vaultsId])

  const totalValue = useMemo(() => {
    if (vaultsId) {
      return holdTotalValue[vaultsId]
    }
    return undefined
  }, [holdTotalValue, vaultsId])

  // useEffect(() => {
  //   if (lpTokenInfo && lpTokenInfo.coin_type) {
  //     setLpTokenInfo(lpTokenInfo)
  //   }
  // }, [lpTokenInfo])

  return {
    fetchVaultInfoFromContract,
    contractVault,
    lpTokenInfo,
    balanceInfo,
    sharePoolRate,
    fetchClmmPoolFromContract,
    holdCoinAmounts,
    totalValue,
    maxPrice,
    currPrice,
    minPrice,
    vaultCoinAmounts,
    vaultsCoinAValue,
    vaultsCoinBValue,
    vaultsCoinPercent,
    holdCoinAValue,
    holdCoinBValue,
    calculateCoinAmounts,
    vaultTvl,
    holdLpAmount: balanceInfo?.balance
  } as ContractVaultInfo
}
