import useUnstableVaultsListStore from '@/store/unstable-vaults/useVaultsList'
import { ContractVaultInfo } from '@/types/vaults'
import { calcCoinProportion } from '@/utils/pool'
import { useGetToken } from '@cetus/hooks/src/useToken'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import { useAccountStore } from '@cetus/stores'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { BalanceInfo, Token } from '@cetus/types'
import { Decimal, formatNumber, fromDecimalsAmountFix } from '@cetus/utils'
import { d, fixCoinType, fromDecimalsAmount, TickMath } from '@cetusprotocol/cetus-sui-clmm-sdk'
import BN from 'bn.js'
import { buildVaultsBalance, VaultsBalance } from 'haedal-vault-sdk'
import { useEffect, useMemo, useState } from 'react'
import { useContractClmmInfo, useContractVault } from './useVaultsHelper'

/**
 * 通过合约数据，计算vaults相关属性
 * @param vaultsId
 * @param poolAddress
 * @param tokenA
 * @param tokenB
 * @param isReverse
 * @returns
 */
export default function useGetContractUnstableVaultInfo(
  vaultsId?: string,
  poolAddress?: string,
  tokenA?: Token,
  tokenB?: Token,
  isReverse: boolean = false,
  isDetail?: boolean
) {
  const { volatileVaultsSdk } = usePeripherySDKStore()
  const { getTokenAmountValue } = useTokenPrice()
  const { currentAccount } = useAccountStore()
  const {
    setContractVaultObjLoading,
    setContractVaultObj,
    setContractClmmPoolObj,
    setContractClmmPoolObjLoading,
    setHoldTotalValue,
    holdTotalValue,
    contractClmmPoolObjLoading,
    contractVaultObjLoading
  } = useUnstableVaultsListStore()

  // **获取 Vault 信息**
  const fetchVaultInfoFromContract = async (vaultsId: string, forceRefresh = false) => {
    setContractVaultObjLoading(true)
    try {
      const info = await volatileVaultsSdk.Vaults.getPool(vaultsId, forceRefresh)
      if (info) setContractVaultObj(info)
    } catch (error) {
      console.error('fetchVaultInfoFromContract error:', error)
    } finally {
      setContractVaultObjLoading(false)
    }
  }

  // **获取 CLMM 池子信息**
  const fetchClmmPoolFromContract = async (clmmPoolId: string, forceRefresh = false) => {
    setContractClmmPoolObjLoading(true)
    try {
      const info = await volatileVaultsSdk.CetusClmmSDK.Pool.getPool(clmmPoolId, forceRefresh)
      if (info) setContractClmmPoolObj(info)
    } catch (error) {
      console.error('fetchClmmPoolFromContract error:', error)
    } finally {
      setContractClmmPoolObjLoading(false)
    }
  }

  // 合约vaults 信息
  const { contractVault } = useContractVault(vaultsId)

  // lp token信息
  const { tokenInfo: lpTokenInfo } = useGetToken(contractVault?.lp_token_type)

  // lp token 余额
  const [balanceInfo, setBalanceInfo] = useState<BalanceInfo>()
  const [isBalanceLoading, setBalanceLoading] = useState(false)
  const [accountBalanceInfo, setAccountBalanceInfo] = useState<VaultsBalance>()
  // **获取 LP 余额**
  const fetchVaultBalance = async (walletAddress: string, vaultsId: string, decimals: string) => {
    setBalanceLoading(true)
    try {
      // 余额是加了未分配的部分的
      const res = await volatileVaultsSdk.Vaults.getOwnerVaultsBalance(walletAddress, vaultsId)
      if (res) {
        const balanceFormat = fromDecimalsAmountFix(res.lp_token_balance, decimals).toString()
        setBalanceInfo({
          balance: res.lp_token_balance,
          balanceFormat,
          balanceDisplay: formatNumber(balanceFormat, undefined, undefined, Decimal.ROUND_DOWN).toString()
        })
        setAccountBalanceInfo(res)
      }
    } catch (error) {
      console.error('fetchVaultBalance error:', error)
    } finally {
      setBalanceLoading(false)
    }
  }

  //持仓百分比
  const sharePoolRate = useMemo(() => {
    if (contractVault && balanceInfo && d(balanceInfo.balance).gt(0)) {
      return d(balanceInfo.balance).div(contractVault.lp_token_treasury).mul(100).toString()
    }

    return '0'
  }, [contractVault, balanceInfo])

  // ClmmPool 信息
  const { contractClmmInfo } = useContractClmmInfo(poolAddress)
  // **计算持有的 Token A/B 数量**
  const getHoldCoinAmounts = (accountBalanceInfo?: VaultsBalance) => {
    if (accountBalanceInfo && tokenA && tokenB && isReverse !== undefined) {
      const coinAmountA = fromDecimalsAmount(accountBalanceInfo.amount_a.toString(), tokenA.decimals).toString()
      const coinAmountB = fromDecimalsAmount(accountBalanceInfo.amount_b.toString(), tokenB.decimals).toString()
      const displayCoinA = isReverse ? coinAmountB : coinAmountA
      const displayCoinB = isReverse ? coinAmountA : coinAmountB
      return {
        coinAmountA,
        coinAmountB,
        displayCoinA,
        displayCoinB
      }
    }
    return { coinAmountA: '0', coinAmountB: '0', displayCoinA: '0', displayCoinB: '0' }
  }

  const [holdCoinAmounts, setHoldCoinAmounts] = useState<{ coinAmountA: string; coinAmountB: string; displayCoinA: string; displayCoinB: string }>()
  useEffect(() => {
    if (accountBalanceInfo && currentAccount?.address) {
      setHoldCoinAmounts(getHoldCoinAmounts(accountBalanceInfo))
    } else {
      setHoldCoinAmounts({ coinAmountA: '0', coinAmountB: '0', displayCoinA: '0', displayCoinB: '0' })
    }
  }, [accountBalanceInfo?.amount_a, accountBalanceInfo?.amount_b, tokenA?.decimals, tokenB?.decimals, isReverse, currentAccount?.address])

  // const holdCoinAmounts = useMemo(() => {
  //   if (accountBalanceInfo) {
  //     console.log(
  //       '🚀🚀🚀 ~ useGetContractVaultInfo.ts:139 ~ holdCoinAmounts ~ getHoldCoinAmounts(accountBalanceInfo):',
  //       getHoldCoinAmounts(accountBalanceInfo)
  //     )
  //     return getHoldCoinAmounts(accountBalanceInfo)
  //   }
  // }, [accountBalanceInfo?.amount_a, accountBalanceInfo?.amount_b, tokenA?.decimals, tokenB?.decimals, isReverse])

  // **计算 CLMM 池子持有的 Token A/B**
  const vaultCoinAmounts = useMemo(() => {
    if (contractVault && contractClmmInfo && !contractVaultObjLoading && !contractClmmPoolObjLoading) {
      const result = buildVaultsBalance('', contractVault.lp_token_treasury, contractVault, contractClmmInfo)
      return getHoldCoinAmounts(result)
    }
    return undefined
  }, [contractVault?.lp_token_treasury, contractClmmInfo?.current_sqrt_price, contractClmmPoolObjLoading, contractVaultObjLoading])

  // 当前价格
  const currPrice = useMemo(() => {
    if (contractClmmInfo && tokenA && tokenB) {
      return TickMath.sqrtPriceX64ToPrice(new BN(contractClmmInfo.current_sqrt_price), tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractClmmInfo?.current_sqrt_price, tokenA?.decimals])

  //最小价格
  const minPrice = useMemo(() => {
    if (contractVault && tokenA && tokenB) {
      return TickMath.tickIndexToPrice(contractVault.clmm_vault.wrapped_position.tick_lower_index, tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractVault?.clmm_vault?.wrapped_position.tick_lower_index, tokenA?.decimals])

  //最大价格
  const maxPrice = useMemo(() => {
    if (contractVault && tokenA && tokenB) {
      return TickMath.tickIndexToPrice(contractVault.clmm_vault.wrapped_position.tick_upper_index, tokenA.decimals, tokenB.decimals).toString()
    }
    return undefined
  }, [contractVault?.clmm_vault.wrapped_position.tick_upper_index, tokenA?.decimals])

  // 用户持有的coin A B 价值
  const holdCoinAValue = getTokenAmountValue(tokenA?.coin_type, holdCoinAmounts?.coinAmountA)
  const holdCoinBValue = getTokenAmountValue(tokenB?.coin_type, holdCoinAmounts?.coinAmountB)
  const [calculateVaultTvlLoading, setCalculateVaultTvlLoading] = useState(true)

  const [vaultsCoinAValue, setVaultsCoinAValue] = useState<string>('0')
  const [vaultsCoinBValue, setVaultsCoinBValue] = useState<string>('0')
  // 池子持有的coin A B 价值
  // const vaultsCoinAValue = getTokenAmountValue(tokenA?.coin_type, vaultCoinAmounts?.coinAmountA)
  // const vaultsCoinBValue = getTokenAmountValue(tokenB?.coin_type, vaultCoinAmounts?.coinAmountB)
  // 由于新增显示剩余可注入数量 所以这里改为获取预言机价格计算 这样计算出来的占比可与剩余可注入一致
  // const [pythPriceMap, setPythPriceMap] = useState<Record<string, Price>>()

  const getVaultsCoinValue = async (coinAmountA: string, coinAmountB: string) => {
    if (tokenA && tokenB) {
      const priceMap = await volatileVaultsSdk.PythPrice.getLatestPrice([fixCoinType(tokenA?.coin_type), fixCoinType(tokenB?.coin_type)], true)
      if (priceMap) {
        const priceA = priceMap[fixCoinType(tokenA?.coin_type)]
        const priceB = priceMap[fixCoinType(tokenB?.coin_type)]
        if (priceA && priceB) {
          const vaultsCoinAValue = d(coinAmountA).mul(priceA.price).toString()
          const vaultsCoinBValue = d(coinAmountB).mul(priceB.price).toString()
          setVaultsCoinAValue(vaultsCoinAValue)
          setVaultsCoinBValue(vaultsCoinBValue)
          setCalculateVaultTvlLoading(false)
        }
      }
    }
  }
  useEffect(() => {
    if (vaultCoinAmounts?.coinAmountA && vaultCoinAmounts?.coinAmountB && isDetail) {
      getVaultsCoinValue(vaultCoinAmounts?.coinAmountA, vaultCoinAmounts?.coinAmountB)
    }
  }, [vaultCoinAmounts?.coinAmountA, vaultCoinAmounts?.coinAmountB])

  // 池子Token占比
  const vaultsCoinPercent = useMemo(() => {
    if (currPrice && vaultCoinAmounts) {
      return calcCoinProportion(vaultCoinAmounts.coinAmountA, vaultCoinAmounts.coinAmountB, currPrice, false)
    }

    return undefined
  }, [currPrice, vaultCoinAmounts])

  // 池子tvl
  // const vaultTvl = useMemo(() => {
  //   return d(vaultsCoinAValue).add(vaultsCoinBValue).toString()
  // }, [vaultsCoinAValue, vaultsCoinBValue])

  const [vaultTvl, setVaultTvl] = useState<string>('')
  useEffect(() => {
    if (vaultsCoinAValue && vaultsCoinBValue) {
      setVaultTvl(d(vaultsCoinAValue).add(vaultsCoinBValue).toString())
    }
  }, [vaultsCoinAValue, vaultsCoinBValue])

  useEffect(() => {
    if (vaultsId && holdCoinAValue && holdCoinBValue) {
      setHoldTotalValue(vaultsId, d(holdCoinAValue).add(holdCoinBValue).toString())
    }
  }, [holdCoinAValue, holdCoinBValue, vaultsId])

  const totalValue = useMemo(() => {
    if (vaultsId) {
      return holdTotalValue[vaultsId]
    }
    return undefined
  }, [holdTotalValue, vaultsId])

  const [hardCapUSD, setHardCapUSD] = useState<string>('')
  const getHardCapUSD = async () => {
    if (contractVault && contractVault.hard_cap && d(contractVault?.hard_cap).gt(0) && tokenA && tokenB) {
      const quoteCoin = contractVault.quote_type == tokenA?.address ? tokenA! : tokenB!
      const priceMap = await volatileVaultsSdk.PythPrice.getLatestPrice([fixCoinType(quoteCoin.coin_type)], true)

      if (priceMap) {
        const price = priceMap[fixCoinType(quoteCoin.coin_type)]
        if (price) {
          const hardCapUSD = d(contractVault.hard_cap)
            .div(10 ** quoteCoin.decimals)
            .mul(price.price)
            .toString()
          console.log('🚀🚀🚀 ~ useGetContractVaultInfo.ts:251 ~ getHardCapUSD ~ hardCapUSD:', hardCapUSD)
          setHardCapUSD(hardCapUSD)
        }
      }
    }
  }
  useEffect(() => {
    console.log('🚀🚀🚀 ~ useGetContractVaultInfo.ts:272 ~ useEffect ~ contractVault:', contractVault)
    if (contractVault?.hard_cap && isDetail) {
      getHardCapUSD()
    }
  }, [contractVault?.hard_cap, isDetail])

  // const hardCapUSD = useMemo(() => {
  //   if (contractVault && contractVault.hard_cap && d(contractVault?.hard_cap).gt(0) && tokenA && tokenB) {
  //     let quoteCoin
  //     if (contractVault.quote_type) {
  //       quoteCoin = contractVault.quote_type == tokenA?.address ? tokenA! : tokenB!
  //     } else {
  //       quoteCoin = getTokenInfo('0xdba34672e30cb065b1f93e3ab55318768fd6fef66c15942c9f7cb846e2f900e7::usdc::USDC')!
  //     }
  //     const maxAum = fromDecimalsAmount(contractVault.hard_cap, quoteCoin.decimals).toString()
  //     // usdc不使用汇率直接为1u
  //     // if (quoteCoin?.coin_type == '0xdba34672e30cb065b1f93e3ab55318768fd6fef66c15942c9f7cb846e2f900e7::usdc::USDC') {
  //     //   maxAumUSD = maxAum
  //     //   return maxAumUSD
  //     // }
  //     const maxAumUSD = getTokenAmountValue(quoteCoin?.coin_type, maxAum)
  //     return maxAumUSD
  //   }
  //   return ''
  // }, [contractVault?.hard_cap, tokenA, tokenB])

  // 当前tvl是否大于硬顶
  const gtAumLimit = useMemo(() => {
    if (vaultsCoinAValue && vaultsCoinBValue && contractVault?.hard_cap && d(contractVault?.hard_cap).gt(0) && tokenA && tokenB) {
      const quoteCoin = contractVault.quote_type == tokenA?.address ? tokenA : tokenB
      const maxAum = fromDecimalsAmount(contractVault.hard_cap, quoteCoin.decimals).toString()
      const maxAumUSD = getTokenAmountValue(quoteCoin?.coin_type, maxAum)
      if (d(vaultsCoinAValue).add(vaultsCoinBValue).gt(maxAumUSD)) {
        return true
      }
    }
    return false
  }, [vaultsCoinAValue, vaultsCoinBValue, contractVault?.hard_cap, tokenA, tokenB])

  useEffect(() => {
    if (!currentAccount?.address) {
      setBalanceInfo(undefined)
      setAccountBalanceInfo(undefined)
    }
  }, [currentAccount])

  const depositRatio = useMemo(() => {
    if (hardCapUSD && vaultTvl) {
      return d(vaultTvl).div(hardCapUSD).mul(100).toString()
    }
  }, [hardCapUSD, vaultTvl])

  const performanceFee = useMemo(() => {
    if (contractVault && d(contractVault.protocol_fee_rate).gt(0)) {
      return d(contractVault.protocol_fee_rate).div(10000).mul(100).toString()
    }
    return '0'
  }, [contractVault])

  const refreshPythPrice = async () => {
    if (tokenA && tokenB) {
      await volatileVaultsSdk.PythPrice.getLatestPrice([fixCoinType(tokenA?.coin_type), fixCoinType(tokenB?.coin_type)], true)
    }
    if (vaultCoinAmounts?.coinAmountA && vaultCoinAmounts?.coinAmountB) {
      await getVaultsCoinValue(vaultCoinAmounts?.coinAmountA, vaultCoinAmounts?.coinAmountB)
    }
    await getHardCapUSD()
  }

  const quoteCoin = useMemo(() => {
    if (contractVault && tokenA && tokenB) {
      return contractVault.quote_type == tokenA?.address ? tokenA : tokenB
    }
    return undefined
  }, [contractVault, tokenA, tokenB])

  const maxCapNum = useMemo(() => {
    if (contractVault?.hard_cap && quoteCoin) {
      return d(contractVault?.hard_cap)
        .div(10 ** quoteCoin.decimals)
        .toString()
    }
    return '0'
  }, [contractVault?.hard_cap, quoteCoin])

  return {
    fetchVaultInfoFromContract,
    contractVault,
    lpTokenInfo,
    balanceInfo,
    sharePoolRate,
    fetchClmmPoolFromContract,
    holdCoinAmounts,
    totalValue,
    maxPrice,
    currPrice,
    minPrice,
    vaultCoinAmounts,
    vaultsCoinAValue,
    vaultsCoinBValue,
    vaultsCoinPercent,
    holdCoinAValue,
    holdCoinBValue,
    fetchVaultBalance,
    isBalanceLoading,
    vaultTvl,
    hardCapUSD,
    gtAumLimit,
    depositRatio,
    performanceFee,
    refreshPythPrice,
    calculateVaultTvlLoading,
    maxCapNum,
    quoteCoin,
    holdLpAmount: balanceInfo?.balance
  } as ContractVaultInfo
}
