import useTransaction from '@/hooks/common/useTransaction'
import useGlobalStore from '@/store/common/global'
import useVaultsListStore from '@/store/vaults/useVaultsList'
import { MsafeTransactionSubType } from '@/types'
import { AssetActionType, VaultCoinAmounts } from '@/types/vaults'
import { formatDescription } from '@/utils'
import { useDebounceFunction } from '@cetus/hooks'
import useTokenPrice from '@cetus/hooks/src/useTokenPrice'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'
import { BalanceChanges, CommonTypeInfo, ToastType, Token, TransactionStatusType } from '@cetus/types'
import { d, formatNumber, fromDecimalsAmountFix, getBalanceChanges } from '@cetus/utils'
import { toDecimalsAmount } from '@cetusprotocol/cetus-sui-clmm-sdk'
import {
  CalculateRemoveAmountParams,
  CalculateRemoveAmountResult,
  InputType,
  WithdrawBothParams,
  WithdrawOneSideParams
} from '@cetusprotocol/vaults-sdk'
import { useEffect, useMemo, useRef, useState } from 'react'
import { v4 } from 'uuid'
import { useFindBestRouting } from '../swap/useFindBestRouting'

export default function useStableVaultsRemove(
  assetAction: AssetActionType,
  autoRefresh: () => void,
  holdCoinAmounts: VaultCoinAmounts,
  holdLpAmount?: string,
  vaultsId?: string,
  tokenA?: Token,
  tokenB?: Token
) {
  const { setDetailRefreshCount } = useVaultsListStore()
  const { fetchTokenPrices, getTokenAmountValue } = useTokenPrice()
  const { vaultsSdk } = usePeripherySDKStore()
  const { vaultsSlippage, mevProtect, maxCapForGas, transactionMode, customGasPrice } = useGlobalStore()
  const { findBestRouters } = useFindBestRouting()

  const [amountInputA, setAmountInputA] = useState<string>('')
  const [amountInputB, setAmountInputB] = useState<string>('')
  const [fixAmountA, setFixAmountA] = useState<boolean>(true)
  const [uuid, setUuid] = useState<string>('')
  const [isPercentInput, setIsPercentInput] = useState<boolean>(false)
  const [removePercent, setRemovePercent] = useState<number>(0)

  const uuidRef = useRef<string>(uuid)
  const fixAmountARef = useRef<boolean>(fixAmountA)
  const amountInputARef = useRef<string>(amountInputA)
  const amountInputBRef = useRef<string>(amountInputB)
  const isPercentInputRef = useRef<boolean>(isPercentInput)
  const removePercentRef = useRef<number>(removePercent)

  useEffect(() => {
    removePercentRef.current = removePercent
  }, [removePercent])

  useEffect(() => {
    isPercentInputRef.current = isPercentInput
  }, [isPercentInput])

  useEffect(() => {
    uuidRef.current = uuid
  }, [uuid])

  useEffect(() => {
    fixAmountARef.current = fixAmountA
  }, [fixAmountA])

  useEffect(() => {
    amountInputARef.current = amountInputA
  }, [amountInputA])

  useEffect(() => {
    amountInputBRef.current = amountInputB
  }, [amountInputB])

  //获取价格
  useEffect(() => {
    const coinTypeList: string[] = []
    if (tokenA && tokenB) {
      coinTypeList.push(tokenA.coin_type)
      coinTypeList.push(tokenB.coin_type)
    }
    if (coinTypeList.length > 0) {
      fetchTokenPrices(coinTypeList)
    }
  }, [tokenA?.coin_type, tokenB?.coin_type])

  // 可用数量
  const [availableAmountA, setAvailableAmountA] = useState<string>('0')
  const [availableAmountB, setAvailableAmountB] = useState<string>('0')
  const [calculateAvailableLoading, setCalculateAvailableLoading] = useState<boolean>(false)

  // 切换资产类型，清空输入框
  useEffect(() => {
    resetInputAmount()
    calculateAvailableAmount()
  }, [assetAction])

  // 资产变更，计算 A B可用数量
  useEffect(() => {
    calculateAvailableAmount()
  }, [holdCoinAmounts.coinAmountA])

  // 计算可用资产
  const calculateAvailableAmount = () => {
    const { coinAmountA, coinAmountB } = holdCoinAmounts
    console.log('🚀 ~ useEffect ~ holdCoinAmounts:', holdCoinAmounts)
    setCalculateAvailableLoading(false)
    if (assetAction === 'both') {
      setAvailableAmountA(coinAmountA)
      setAvailableAmountB(coinAmountB)
      return
    }

    // 利用findRouter 转化为 单一资产
    const isOnlyReceiveCoinA = assetAction === tokenA?.coin_type
    const fromAmount = isOnlyReceiveCoinA ? coinAmountB : coinAmountA
    let targetAmount = isOnlyReceiveCoinA ? coinAmountA : coinAmountB
    const fromToken = isOnlyReceiveCoinA ? tokenB! : tokenA!
    const toToken = isOnlyReceiveCoinA ? tokenA! : tokenB!
    if (d(fromAmount).gt(0)) {
      setCalculateAvailableLoading(true)
      setAvailableAmountA('0')
      setAvailableAmountB('0')
      findBestRouters({
        fromToken,
        toToken,
        amount: toDecimalsAmount(fromAmount, fromToken.decimals).toString(),
        providersKeys: vaultsSdk.sdkOptions.aggregator.providers!,
        by_amount_in: true,
        uuid
      })
        .then(result => {
          console.log('🚀 ~ calculateAvailableAmount ~ uuid:', {
            result
          })
          if (result.toAmountUi) {
            targetAmount = d(targetAmount).add(result.toAmountUi).toString()
          }
          if (isOnlyReceiveCoinA) {
            setAvailableAmountA(targetAmount)
          } else {
            setAvailableAmountB(targetAmount)
          }
        })
        .finally(() => {
          setCalculateAvailableLoading(false)
        })
    } else {
      if (isOnlyReceiveCoinA) {
        setAvailableAmountA(targetAmount)
      } else {
        setAvailableAmountB(targetAmount)
      }
    }
  }

  // 价值
  const amountValueA = getTokenAmountValue(tokenA?.coin_type, amountInputA)
  const amountValueB = getTokenAmountValue(tokenB?.coin_type, amountInputB)
  // 总价值ß
  const inputTotalValue = useMemo(() => {
    return d(amountValueA || '0')
      .add(amountValueB || '0')
      .toString()
  }, [amountValueA, amountValueB])

  // 预计算结果
  const [preCalculateLoading, setPreCalculateLoading] = useState<boolean>(false)
  const [calculateResult, setCalculateResult] = useState<CalculateRemoveAmountResult | undefined>(undefined)

  /**
   * 预计算
   * @param amount
   * @param isPercentInput
   * @param fix_amount_a
   * @param uuid
   */
  const preCalculate = async (amount: string, is_ft_input: boolean, fix_amount_a: boolean, uuid: string, removePercent?: number) => {
    if (tokenA && tokenB && vaultsId && holdLpAmount) {
      // 非百分比输入，要判断余额是否足够
      if (!removePercent) {
        if (fix_amount_a) {
          if (d(amount).gt(availableAmountA)) {
            setUuid('')
            setCalculateResult(undefined)
            setPreCalculateLoading(false)
            return
          }
        } else {
          if (d(amount).gt(availableAmountB)) {
            setUuid('')
            setCalculateResult(undefined)
            setPreCalculateLoading(false)
            return
          }
        }
      }

      setDetailRefreshCount(0)
      if (+amountInputARef.current || +amountInputBRef.current || removePercent) {
        setPreCalculateLoading(true)
      }
      const fixToken = fix_amount_a ? tokenA : tokenB
      const inputAmount = is_ft_input ? amount : toDecimalsAmount(amount, fixToken.decimals).toFixed(0)
      const params: CalculateRemoveAmountParams = {
        vault_id: vaultsId,
        fix_amount_a,
        input_amount: inputAmount,
        slippage: Number(vaultsSlippage),
        request_id: uuid,
        side: assetAction === 'both' ? InputType.Both : InputType.OneSide,
        is_ft_input,
        max_ft_amount: holdLpAmount
      }
      console.log('🚀 ~ preCalculate ~ params:', params, uuid, removePercent)
      try {
        const res = await vaultsSdk.Vaults.calculateWithdrawAmount(params)
        console.log('🚀 ~ preCalculate ~ res:', {
          ...res,
          uuid,
          assetAction
        })

        if (uuidRef.current === uuid) {
          const amountA = fromDecimalsAmountFix(res.amount_a.toString(), tokenA.decimals).toString()
          const amountB = fromDecimalsAmountFix(res.amount_b.toString(), tokenB.decimals).toString()
          // 双边
          if (assetAction === 'both') {
            if (is_ft_input) {
              setAmountInputB(amountB)
              setAmountInputA(amountA)
              if (removePercent === 100) {
                // 由于价差原因，当移除100% 时， 需要修改最大可用
                setAvailableAmountA(amountA)
                setAvailableAmountB(amountB)
              }
              setCalculateResult(res)
            } else {
              if (fix_amount_a) {
                if (+amountInputARef.current) {
                  setAmountInputB(amountB)
                  setCalculateResult(res)
                } else {
                  setAmountInputB('')
                }
              } else {
                if (+amountInputBRef.current) {
                  setAmountInputA(amountA)
                  setCalculateResult(res)
                } else {
                  setAmountInputA('')
                }
              }
            }
          }
          // 单边
          if (assetAction !== 'both') {
            if (removePercent) {
              const amountA = fromDecimalsAmountFix(
                d(res.amount_a)
                  .add(res.swap_result?.swap_out_amount || '0')
                  .toString(),
                tokenA.decimals
              ).toString()
              if (fix_amount_a) {
                setAmountInputA(amountA)
                setAmountInputB('')
                if (removePercent === 100) {
                  res.burn_ft_amount = holdLpAmount
                  setAvailableAmountA(amountA)
                }
              } else {
                const amountB = fromDecimalsAmountFix(
                  d(res.amount_b)
                    .add(res.swap_result?.swap_out_amount || '0')
                    .toString(),
                  tokenB.decimals
                ).toString()
                setAmountInputA('')
                setAmountInputB(amountB)
                if (removePercent === 100) {
                  res.burn_ft_amount = holdLpAmount
                  setAvailableAmountB(amountB)
                }
              }
            }
            setCalculateResult(res)
          }
        }
      } catch (error) {
        console.log('🚀 ~ preCalculate ~ error:', error)
        setCalculateResult(undefined)
      } finally {
        setPreCalculateLoading(false)
      }
    }
  }
  const debouncedPreCalculate = useDebounceFunction(preCalculate, 500)

  // 输入数量监听
  const handleAmountInputChange = (
    amount: string,
    fix_amount_a: boolean,
    removeMax: boolean = false,
    isHalfClickMax: boolean = false,
    resetTargetInput: boolean = true
  ) => {
    // 移除全部，走LP 移除
    if (removeMax) {
      handlePercentInputChange(100)
      return
    }

    if (isHalfClickMax) {
      handlePercentInputChange(50)
      return
    }

    setFixAmountA(fix_amount_a)
    setIsPercentInput(false)

    if (fix_amount_a) {
      setAmountInputA(amount)
      if (resetTargetInput) {
        setAmountInputB('')
      }
    } else {
      setAmountInputB(amount)
      if (resetTargetInput) {
        setAmountInputA('')
      }
    }

    if (+amount) {
      const uuid = v4()
      setUuid(uuid)
      debouncedPreCalculate(amount, false, fix_amount_a, uuid)
    } else {
      resetInputAmount()
    }
  }

  // 百分比输入监听
  const handlePercentInputChange = (percent: number) => {
    setIsPercentInput(true)
    setRemovePercent(percent)
    if (holdLpAmount && +holdLpAmount) {
      if (percent === 0) {
        resetInputAmount()
      } else {
        const fix_amount_a = assetAction === tokenA?.coin_type
        setFixAmountA(fix_amount_a)
        // 双边和100% 按照LP 移除
        if (percent === 100 || assetAction === 'both') {
          const uuid = v4()
          setUuid(uuid)
          const removeLpAmount = percent === 100 ? holdLpAmount : d(holdLpAmount).mul(percent).div(100).toFixed(0)
          console.log('🚀🚀🚀 ~ useVaultsRemove.ts:351 ~ handlePercentInputChange ~ removeLpAmount:', removeLpAmount)
          debouncedPreCalculate(removeLpAmount, true, fix_amount_a, uuid, percent)
        } else {
          setFixAmountA(fix_amount_a)
          // 单边  按照最大金额的百分比移除
          const getAmount = d(assetAction === tokenA?.coin_type ? availableAmountA : availableAmountB)
            .mul(percent)
            .div(100)
            .toFixed()
          debouncedPreCalculate(getAmount, false, fix_amount_a, uuid, percent)
        }
      }
    }
  }

  // 重新计算
  const reCalculateResult = () => {
    console.log('🚀 ~ reCalculateResult ~ isPercentInput:', { isPercentInput, removePercent })
    if (isPercentInput) {
      handlePercentInputChange(removePercent)
    } else {
      const amount = fixAmountARef.current ? amountInputARef.current : amountInputBRef.current
      handleAmountInputChange(amount, fixAmountARef.current)
    }
  }

  // 不是百分比输入，则需要计算百分比进度
  useEffect(() => {
    if (!isPercentInput) {
      if (calculateResult && holdLpAmount && +holdLpAmount) {
        let percent = Number(d(calculateResult.burn_ft_amount).div(holdLpAmount).mul(100))
        if (percent > 100) {
          percent = 100
        }
        setRemovePercent(percent)
      } else {
        setRemovePercent(0)
      }
    }
  }, [calculateResult?.burn_ft_amount])

  const [submitLoading, setSubmitLoading] = useState<boolean>(false)
  const { signAndExecuteTransaction, transactionConfirmation, transactionRejected } = useTransaction()
  // 提交交易
  const doRemoveAction = async () => {
    if (vaultsId && calculateResult && holdLpAmount) {
      setSubmitLoading(true)

      let param: WithdrawBothParams | WithdrawOneSideParams

      if (assetAction === 'both') {
        param = {
          vault_id: vaultsId,
          ft_amount: calculateResult?.burn_ft_amount,
          slippage: Number(vaultsSlippage)
        }
      } else {
        const fix_amount_a = assetAction === tokenA?.coin_type
        if (removePercent === 100) {
          param = {
            vault_id: vaultsId,
            is_ft_input: true,
            input_amount: holdLpAmount,
            slippage: Number(vaultsSlippage),
            max_ft_amount: holdLpAmount,
            fix_amount_a
          }
        } else {
          const input_amount = toDecimalsAmount(fixAmountA ? amountInputA : amountInputB, 9)
          param = {
            vault_id: vaultsId,
            is_ft_input: false,
            input_amount: input_amount.toString(),
            slippage: Number(vaultsSlippage),
            max_ft_amount: holdLpAmount,
            fix_amount_a
          }
        }
      }

      console.log('🚀 ~ doRemoveAction ~ params:', param)

      // 构建 交易提示
      const toastInfo: ToastType = {
        getShowInfo: (status: TransactionStatusType, balanceChanges?: Record<string, BalanceChanges>) => {
          const description =
            'Withdraw ' +
            [formatDescription(amountInputA, tokenA?.symbol), formatDescription(amountInputB, tokenB?.symbol)].filter(Boolean).join(' and ')

          const info: CommonTypeInfo = {
            modalDescriptionText: description,
            toastDescriptionContent: description
          }

          if (status === 'success') {
            let amountA = amountInputA
            let amountB = amountInputB

            if (balanceChanges) {
              amountA = d(amountInputA).gt(0) ? getBalanceChanges(balanceChanges, tokenA) || amountInputA : ''
              amountB = d(amountInputB).gt(0) ? getBalanceChanges(balanceChanges, tokenB) || amountInputB : ''
            }

            const description =
              'Removed ' + [formatDescription(amountA, tokenA?.symbol), formatDescription(amountB, tokenB?.symbol)].filter(Boolean).join(' and ')

            info.toastDescriptionContent = description
            info.modalDescriptionText = description
          }

          return info
        }
      }
      transactionConfirmation(toastInfo)

      try {
        const txb = await vaultsSdk.Vaults.withdraw(param)
        const res = await signAndExecuteTransaction(txb, toastInfo, {
          useMev: mevProtect,
          txAction: 'signTransactionBlock',
          useFastMode: transactionMode === 'Fast Mode',
          maxCapForGas,
          customGasPrice,
          msafeParams: {
            action: MsafeTransactionSubType.RemoveVaultsPosition,
            txbParams: param
          }
        })

        if (res) {
          resetInputAmount()
        } else {
          reCalculateResult()
        }
        autoRefresh()
      } catch (error) {
        console.log('🚀 ~ doAddAction ~ error:', error)
        transactionRejected(toastInfo)
      }
    }
  }

  // 清空输入框
  const resetInputAmount = () => {
    setAmountInputA('')
    setAmountInputB('')
    setRemovePercent(0)
    setPreCalculateLoading(false)
    setCalculateResult(undefined)
    setUuid('')
  }

  // 单边提示文案
  const showOnlySideTips = useMemo(() => {
    if (calculateResult && calculateResult.swap_result && assetAction !== 'both' && tokenA && tokenB) {
      const amount = fixAmountA ? amountInputA : amountInputB

      const fixToken = fixAmountARef.current ? tokenA : tokenB
      const targetToken = fixAmountARef.current ? tokenB : tokenA

      const swapInAmount = fromDecimalsAmountFix(calculateResult.swap_result.swap_in_amount, fixToken.decimals)

      const swapOutAmount = fromDecimalsAmountFix(
        d(calculateResult.swap_result.swap_out_amount)
          .mul(1 - Number(vaultsSlippage))
          .toString(),
        targetToken.decimals
      )

      console.log('vaultsSlippage: ', {
        vaultsSlippage,
        calculateResult,
        swapOutAmount
      })

      return `To withdraw ${formatNumber(amount)} ${fixToken?.symbol}, ${formatNumber(swapInAmount)} ${targetToken?.symbol} in the pool will be traded for ${swapOutAmount} ${fixToken?.symbol}`
    }
    return undefined
  }, [assetAction, calculateResult, amountInputA, amountInputB])

  return {
    handleAmountInputChange,
    fixAmountA,
    amountInputA,
    amountInputB,
    availableAmountA,
    availableAmountB,
    amountValueA,
    amountValueB,
    preCalculateLoading,
    inputTotalValue,
    calculateResult,
    showOnlySideTips,
    doRemoveAction,
    handlePercentInputChange,
    isPercentInput,
    removePercent,
    reCalculateResult,
    calculateAvailableAmount,
    calculateAvailableLoading
  }
}
