import { AssetActionType } from '@/types/vaults'
import { useAccountStore } from '@cetus/stores'
import { Token } from '@cetus/types'
import { d } from '@cetus/utils'
import { CoinAssist } from '@cetusprotocol/cetus-sui-clmm-sdk'
import { useMemo } from 'react'

export default function useStableAddButtonStatus(
  isCheckedZAP: boolean,
  assetAction: AssetActionType,
  isAdd: boolean,
  amountInputA: string,
  amountInputB: string,
  fixAmountA: boolean,
  balanceA?: string,
  balanceB?: string,
  tokenA?: Token,
  tokenB?: Token
) {
  const { currentAccount } = useAccountStore()

  // 按钮状态
  const submitBtnInfo = useMemo(() => {
    const btnInfo: {
      text?: string
      disabled: boolean
    } = {
      text: undefined,
      disabled: false
    }

    // 判断钱包
    if (!currentAccount?.address) {
      btnInfo.text = 'Connect Wallet'
      btnInfo.disabled = false
      return btnInfo
    }

    if (!tokenA || !tokenB) {
      btnInfo.disabled = true
      return btnInfo
    }

    // 判断输入
    // 双边
    if (assetAction === 'both') {
      if (!+amountInputA && !+amountInputB) {
        btnInfo.text = 'Enter an amount'
        btnInfo.disabled = true
        return btnInfo
      }
    }
    // 单边输入
    if (assetAction !== 'both') {
      if (!(+amountInputA || +amountInputB)) {
        btnInfo.text = 'Enter an amount'
        btnInfo.disabled = true
        return btnInfo
      }
      // 单边添加流动性。 最少10 sui
      if (isAdd) {
        const isSUI = CoinAssist.isSuiCoin(assetAction)
        if (
          (isSUI && assetAction === tokenA?.coin_type && d(amountInputA).lt(3)) ||
          (isSUI && assetAction === tokenB?.coin_type && d(amountInputB).lt(3))
        ) {
          btnInfo.text = 'Minimum deposit 3 SUI'
          btnInfo.disabled = true
          return btnInfo
        }
      }
    }

    // 判断余额
    // 双边
    if (assetAction === 'both') {
      if (d(amountInputA).gt(balanceA || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenA?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
      if (d(amountInputB).gt(balanceB || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenB?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
    }
    // 单边
    if (assetAction !== 'both') {
      if (fixAmountA && d(amountInputA).gt(balanceA || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenA?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
      if (!fixAmountA && d(amountInputB).gt(balanceB || '0')) {
        btnInfo.disabled = true
        btnInfo.text = isAdd ? `Insufficient ${tokenB?.symbol} Balance` : 'Invalid Amount'
        return btnInfo
      }
    }

    if (isCheckedZAP && assetAction !== 'both') {
      btnInfo.text = isAdd ? `Zap In` : 'Zap Out'
      return btnInfo
    }

    console.log('🚀🚀🚀 ~ submitBtnInfo ~ btnInfo:', btnInfo)
    return btnInfo
  }, [currentAccount?.address, assetAction, amountInputA, amountInputB, balanceA, balanceB])

  return {
    btnText: submitBtnInfo.text,
    btnDisabled: submitBtnInfo.disabled
  }
}
