import { create, StateCreator } from 'zustand'
import { persist } from 'zustand/middleware'

interface VaultsAddState {
  amountInputA: string
  amountInputB: string
  setAmountInputA: (amountInputA: string) => void
  setAmountInputB: (amountInputB: string) => void
}

const store: StateCreator<VaultsAddState> = (set, get) => ({
  amountInputA: '',
  amountInputB: '',
  setAmountInputA: (amountInputA: string) => set(() => ({ amountInputA })),
  setAmountInputB: (amountInputB: string) => set(() => ({ amountInputB }))
})

const useVaultsAddStore = create(
  persist(store, {
    name: 'useVaultsAddStore'
  })
)
export default useVaultsAddStore
