import useSwapWidgetStore from '@/store/swap-widget/swapWidget'
import { VaultApiInfo } from '@/types/vaults'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { useEffect, useState } from 'react'
import useGetVaultList from './useGetVaultList'

export default function useGetApiStableVaultInfo(vaultsId?: string) {
  const { getVaultList, getVaultListFromCache } = useGetVaultList()

  const [apiVaultInfo, setApiVaultInfo] = useState<VaultApiInfo | undefined>(undefined)
  const [vaultInfoLoading, setVaultInfoLoading] = useState<boolean>(false)
  const { setFromCoin, setToCoin } = useSwapWidgetStore()

  // 获取单个vault api信息，报错基础信息和统计信息
  const getApiVaultInfo = async (vaultsId: string, refresh: boolean) => {
    if (apiVaultInfo === undefined || refresh) {
      setVaultInfoLoading(true)
    }

    const res = await getVaultList({
      sortRule: 'desc',
      sortType: 'apr'
    })

    const vaultApiInfo = res?.filter((item: any) => item.vaultsId === vaultsId)?.[0] || null

    setVaultInfoLoading(true)

    if (vaultApiInfo) {
      setApiVaultInfo(vaultApiInfo)
    }
    return vaultApiInfo
  }

  const getApiVaultInfoFromCache = (vaultsId: string) => {
    const res = getVaultListFromCache({
      sortRule: 'desc',
      sortType: 'apr'
    })
    const vaultApiInfo = res?.filter((item: any) => item.vaultsId === vaultsId)?.[0] || null
    return vaultApiInfo
  }

  useEffect(() => {
    if (vaultsId) {
      // 优先读取缓存
      const info = getApiVaultInfoFromCache(vaultsId)
      if (info) {
        setApiVaultInfo(info)
      }
      // 异步加载
      getApiVaultInfo(vaultsId, false)
    }
  }, [vaultsId])

  useEffect(() => {
    if (apiVaultInfo) {
      setFromCoin(apiVaultInfo?.displayTokenA)
      setToCoin(apiVaultInfo?.displayTokenB)
    }
    return () => {
      setFromCoin(envConfigs.clmm_swap.from_coin)
      setToCoin(envConfigs.clmm_swap.to_coin)
    }
  }, [apiVaultInfo])

  return {
    vaultInfoLoading,
    apiVaultInfo,
    getApiVaultInfo
  }
}
