// api信息
import useGetApiVaultInfo from '../vaults/useGetApiVaultInfo'
// 合约信息
import useGetContractUnstableVaultInfo from '../unstable-vault/useGetContractVaultInfo'
import useGetContractStableVaultInfo from '../vaults/useGetContractVaultInfo'
// 添加
import useUnstableVaultsAdd from '../unstable-vault/useVaultsAdd'
import useStableVaultsAdd from '../vaults/useVaultsAdd'
// 按钮状态
import useUnstableAddButtonStatus from '../unstable-vault/useVaultsButtonStatus'
import useStableAddButtonStatus from '../vaults/useVaultsButtonStatus'
// 移除
import useUnstableVaultsRemove from '../unstable-vault/useVaultsRemove'
import useStableVaultsRemove from '../vaults/useVaultsRemove'
// 获取vault 合约信息
const useGetContractVaultInfo = (isUnstable: boolean) => {
  return isUnstable ? useGetContractUnstableVaultInfo : useGetContractStableVaultInfo
}
// vault添加
const useVaultsAdd = (isUnstable: boolean) => {
  return isUnstable ? useUnstableVaultsAdd : useStableVaultsAdd
}
// vault按钮状态
const useAddButtonStatus = (isUnstable: boolean) => {
  return isUnstable ? useUnstableAddButtonStatus : useStableAddButtonStatus
}
// 移除
const useVaultsRemove = (isUnstable: boolean) => {
  return isUnstable ? useUnstableVaultsRemove : useStableVaultsRemove
}

const vaultInterface = {
  useGetApiVaultInfo,
  useGetContractVaultInfo,
  useVaultsAdd,
  useAddButtonStatus,
  useVaultsRemove
}

export default vaultInterface
